.divCard {
  border-radius: 15px;
  height:33vh;
  width:14vw;
  margin-left: 0px;
  margin-right: 0px;
  background-color: rgba(0, 0, 0, 0.452);
  color: #f5efe8;
  transition: 0.5s;
  font-family:"Consolas";
}

.divCard:hover{
  background-color: rgba(255, 255, 255, 0.452);
  color: black
}

.divTop {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra los elementos verticalmente */
  height: 10vh;
  padding: 0;
}

.divTitles {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra los elementos horizontalmente */
  text-align: center; /* Centra el texto horizontalmente */
  margin-top: 4vh; /* Ajusta el valor seg√∫n tus necesidades */
  padding: 2%;
}

.divImg {
  display: flex;
  justify-content: left;
  position: relative;
  top: 4vh;

}

.imgCard {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  object-fit: cover;
  border-radius: 20px;
  height: 15vh;
  width: 10vw;
  align-self: center; 
}

.title {
  margin-top: 0.8vh;
  margin-bottom: 0.3vh;
  font-size: 2.5vh;
}

.subtitle {
  margin-top: 0.3vh; 
  font-size: 1.7vh;
}