.landing {
  text-align: center;
  display: flex;
  flex-direction: column;
  background-image: url(https://www.wallart.com/media/catalog/product/cache/871f459736130e239a3f5e6472128962/a/d/adobestock_190060451.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: auto;
  min-height: 100vh;
  background-position-x: center;
  background-position-y: center;

}

.shadow {
  background-color: rgba(0, 0, 0, 0.418);
  background-size: cover;
  min-height: 100vh;
}

.text {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  width: 55%;
  margin-left: 1rem;
  margin-bottom: 1rem;
  font-family: "Consolas";
  /* font-size: 30px;
    color: rgb(0, 0, 0);
    line-height: 0.1em;
    padding-top: 120px;
    font-weight: bold; */

  margin-top: 9%;
}

.textBig {
  font-size: 4.5vh;
  margin-top: 0;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-content: center;
}

.textsmall {
  font-size: 4vh;
  margin-bottom: 0;
  text-align: center;
  color: rgb(0, 0, 0);
  font-weight: bold;
  margin-bottom: 0.8rem;
}

h5 {
  margin: 0;
}

h1 {
  margin: 0;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: black;
  width: fit-content;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-content: center;
}

.imagen {
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-content: center;
}

.img {
  height: 4vh;
  width: 4vh;
  margin-left: 0.5rem;
  margin-right: 0;
  transition: 0.5s;
  border-radius: 20px;

}

.alink:hover {
  filter: invert(100%)
}

.topLeft {
  display: flex;
  flex-direction: row;
  margin-left: 40px;
  margin-top: 35px;

}

.rosa {
  margin-top: 30px;
  height: 25vh;
  transition: 0.5s;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-content: center;
}

.rosa:hover {
  filter: invert(100%)
}